.chart-container {
  height: 100%;
  display: flex;
  align-items: flex-end;
  position: relative;
  width: 70%;
}

.chart {
  position: absolute;
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: space-around;
  align-items: flex-end;
  gap: 2%;
}

.column-title {
  position: absolute;
  transform: rotateZ(270deg);
  white-space: nowrap;
  transform-origin: 0% 50%;
  bottom: 0px;
  left: 50%;
  color: white;
  text-transform: uppercase;
  font-size: min(5vh, 5vw);
  line-height: 0%;
}

.column-title-container {
  flex: 1;
}

.column {
  height: 100px;
  width: 100%;
  background: red;
  position: relative;
  overflow: hidden;
  flex: 1;
  border-top-left-radius: 999px;
  border-top-right-radius: 999px;
  display: flex;
  flex-direction: column;
}

.value-container {
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  position: relative;
  aspect-ratio: 1 / 1;
  z-index: 1;
}

.value {
  white-space: nowrap;
  color: white;
  text-transform: uppercase;
  font-size: 4vw;
}

.circle-container {
  display: flex;
  width: 100%;
  justify-content: space-around;
  gap: 2%;
}

.circle {
  width: 100%;
  border-radius: 50%;
  aspect-ratio: 1 / 1;
}